body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch_minutes-hand {
  z-index: 1;
  transform-origin: 50% 100%;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%)rotate(0);
}

.stopwatch_seconds-hand {
  z-index: 2;
  transform-origin: 50% 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%)rotate(0);
}

.stopwatch_center-circle {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch--speed-up .stopwatch_minutes_hand {
  animation: 600s steps(60, end) infinite rotate;
}

.stopwatch--speed-up .stopwatch_seconds_hand {
  animation: 10s linear infinite rotate;
}

@keyframes rotate {
  from {
    transform: translate(-50%)rotate(0);
  }

  to {
    transform: translate(-50%)rotate(360deg);
  }
}
